@font-face {
  font-family: "advertisingbold";
  src: url("../fonts/advertisingbold.ttf");
}

@font-face {
  font-family: "ae_AlMohanad";
  src: url("../fonts/ae_AlMohanad.ttf");
}

body {
  font-family: Arial, Helvetica, sans-serif, "ae_AlMohanad";
  background-color: #f9f9f9;
  font-size: 17px;
}

body {
  min-height: 100vh;
}

ul,
ol {
  list-style: none;
  padding-left: 0;
  padding-right: 0;
}

.partner .new-costumer {
  padding: 20px 0;
  background-color: #ddd;
}

.partner .new-costumer .logo-lang .logo {
  width: 150px;
}

.partner .new-costumer .logo-lang .lang-portal {
  display: flex;
  align-items: center;
  justify-content: end;
}

.partner .new-costumer .logo-lang .lang-portal .lang,
.partner .new-costumer .logo-lang .lang-portal .portal {
  text-decoration: none;
  color: #fff;
  font-size: 1.3rem;
  font-weight: 500;
}

.partner .new-costumer .logo-lang .lang-portal .lang {
  margin: 0 10px;
  transition: all 0.3s ease-in-out;
}

.partner .new-costumer .logo-lang .lang-portal .lang:hover {
  color: #ff5a00;
  transition: color 0.3s ease-in-out;
}

.partner .new-costumer .logo-lang .lang-portal .portal {
  border: 3px solid #fff;
  border-radius: 5px;
  padding: 5px 10px;
  transition: all 0.3s ease-in-out;
}

.partner .new-costumer .logo-lang .lang-portal .portal:hover {
  border: 3px solid #ff5a00;
  background-color: #ff59002e;
  transition: all 0.3s ease-in-out;
}

.partner .new-costumer .join-us {
  padding-top: 2rem;
}

.partner .new-costumer .join-us .join-us__text h2 {
  margin-top: 40px;
  font-size: 3.5rem;
  font-weight: bold;
  color: #fff;
}

.partner .new-costumer .join-us .join-us__text h5 {
  font-size: 2.2rem;
  color: #fff;
}

.partner .new-costumer .join-us .join-us__form h3 {
  text-align: center;
  color: #fff;
  font-size: 2.7rem;
  font-weight: bold;
}

.partner .new-costumer .join-us .join-us__form form {
  background-color: #fff;
  border-radius: 5px;
  padding: 30px 20px;
}

.partner .new-costumer .join-us .join-us__form form p {
  color: #3b3b3b;
  font-size: 1.5rem;
}

.partner .new-costumer .join-us .join-us__form form .form-group input {
  border-radius: 4px;
  background-clip: padding-box;
  border: 1px solid #dddbda;
}

.partner .new-costumer .join-us .join-us__form form .form-group input:focus {
  border: 1px solid #ff5a00;
  box-shadow: none;
  transition: all 0.3s ease-in-out;
}

.partner .new-costumer .join-us .join-us__form form .btn-primary--orange {
  background-color: #ff5a00;
  border-color: #ff5a00;
  width: 10rem;
  font-size: 1.5rem;
  padding: 2px;
}

.partner .feature {
  padding: 100px 0 50px 0;
}

.partner .feature h2 {
  color: #ff5a00;
  font-size: 3.5rem;
}

.partner .feature h3 {
  padding-top: 30px;
  padding-bottom: 20px;
  text-align: center;
  font-size: 2rem;
  font-weight: bold;
}

.partner .feature .fet-box {
  text-align: center;
}

.partner .feature .fet-box .fet-icon img {
  max-width: 150px;
}

.partner .benfit {
  text-align: center;
}

.partner .benfit h3 {
  padding-top: 30px;
  padding-bottom: 20px;
  font-size: 2rem;
  font-weight: bold;
}

.partner .benfit .benfit__box {
  padding: 10px 15px;
  border-bottom: 3px solid #ff5a00;
  box-shadow: 0px 0px 3px 2px rgba(94, 94, 94, 0.75);
  border-radius: 5px;
  margin-bottom: 30px;
}

.partner .benfit .benfit__box h4 {
  color: #ff5a00;
}

.partner .about {
  text-align: center;
}

.partner .about h3 {
  padding-top: 30px;
  padding-bottom: 20px;
  font-size: 2rem;
  font-weight: bold;
}

.partner .ready-join {
  padding: 50px 0;
  text-align: center;
}

.partner .ready-join h3 {
  padding-top: 30px;
  padding-bottom: 20px;
  font-size: 2rem;
  font-weight: bold;
}

.partner .ready-join .btn-join {
  background-color: #ff5a00;
  border-color: #ff5a00;
  font-size: 1.5rem;
  padding: 2px;
  padding: 5px 10px;
  width: 15rem;
  display: inline-block;
  color: #fff;
  border-radius: 5px;
}

.partner .counter {
  padding-top: 50px;
}

.partner .counter .counter__box {
  text-align: center;
}

.partner .counter .counter__box .counter__number {
  color: #ff5a00;
  font-size: 4rem;
  font-weight: bold;
}

.partner .counter .counter__box .text {
  font-size: 1.5rem;
}

.partner footer {
  background-color: #1f2533;
  padding: 25px 0;
}

.partner footer h4,
.partner footer a {
  color: #fff;
}

.partner footer .footer-logo {
  width: 150px;
}
